// mint-ui 
import Vue from 'vue';
import { Toast } from 'mint-ui';
import 'mint-ui/lib/toast/style.css';

// vue-awesome
import icon from 'vue-awesome/components/Icon';
import 'vue-awesome/icons/angle-right';
import 'vue-awesome/icons/exclamation';
import 'vue-awesome/icons/circle-o';
import 'vue-awesome/icons/check-square';

//  ICON
import headphones from './../../assets/headphones.vue'; // 
import integration from './../../assets/integration.vue'; // 
import coupon2 from './../../assets/coupon2.vue'; // 
import VIP from './../../assets/VIP.vue'; // 
import personnel from './../../assets/personnel.vue'; // 
import add from './../../assets/add.vue'; // 
import alreadyReserved from './../../assets/alreadyReserved.vue'; // 
import payment2 from './../../assets/payment2.vue'; // 
import addedServices from './../../assets/addedServices.vue'; // 
import evaluate2 from './../../assets/evaluate2.vue'; // 
import financialRecords from './../../assets/financialRecords.vue'; // 
import evaluate from './../../assets/evaluate.vue'; // 
import invoice from './../../assets/invoice.vue'; // 
import collection from './../../assets/collection.vue'; // 

import tabbar from './../../components/TabBar.vue';


import ajaxs from './ajaxs.js';

export default {
  name: 'user',

  components: {
    //  ICON
    headphones, integration, coupon2, VIP, personnel, add,
    alreadyReserved, payment2, addedServices, evaluate2,
    financialRecords, evaluate, invoice, collection,
    // 
    tabbar,
    // vue-awesome
    icon,
  },

  data () {
    return {
      pictures: { // 
        citySketch: require('./../../assets/sketch-picture.png'), // 
      },
    }
  },

  computed: {
    loginIofor() { //  
      return this.$store.state.user.info //   false
    },

    userRelated() { //   
      let VIP = {
        '1': '',
        '2': '',
        '3': '',
        '0': ''
      }

      let related = {
        points: this.$store.state.user.points, // 
        coupons: this.$store.state.user.coupons.count, // 
        VIP: { // VIP
          grade: this.$store.state.user.VIP.grade,
          name: VIP[this.$store.state.user.VIP.grade]
        }
      }
      return related
    },
  },

  methods: {
    tabbarClick(selectIndex) { //  tabbar 
      if (selectIndex === 'home') { //   home  
        this.$router.push('/home');
      }
    },

    callService() { // 
      window.location.href = 'tel://4001106558';
    },

    jumpToUrl(url) { //   url 
      window.location.href = url;
    },

    showNotCompleted(message) { // 
      Toast({
        message: message ? message : '',
        duration: 1000
      });
    }
  },
}




